.MainList {
  border-radius: 12px;
  padding: 12px;
  margin: 12px 0;
  background-color: white;
}
.MainList p {
  margin-bottom: 0;
  text-align: center;
}
.MainList ul {
  list-style-type: none;
  display: flex;
  flex-direction: column;
}

.MainList ul li {
  padding: 8px;
  border-bottom: 1px solid rgb(237, 237, 237);
  /* background-color: blue; */
  /* padding-bottom: 0; */
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.MainList ul li .appProfile {
  display: flex;
  align-items: center;
  justify-content: center;
}

.MainList ul li .imgContainer {
  padding: 2px;
  background: whitesmoke;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 12px;
  border-radius: 12px;
}
.MainList ul li img {
  width: 50px;
  height: 50px;
}
