.SideBarCon {
  position: fixed;
  top: 0;
  background: rgba(0, 0, 0, 0.6);
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.3s;
  user-select: none;
}
.SideBar {
  background: black;
  color: white;
  padding-top: 1.5rem;
  transition: 0.3s;
  height: 100vh;
  width: 60%;
}
.SideBarClose {
  background-color: rgba(0, 0, 0, 0.605);
  height: 100vh;
  width: 40%;
  padding: 1.8rem;
  padding-top: 1.5rem;
  color: white;
  text-align: right;
  font-size: 1.5rem;
}
.SideBar ul {
  padding: 0px 32px;
}

.SideBar ul li {
  border-radius: 6px;
  padding: 8px 12px;
  margin-bottom: 8px;
  background-color: rgba(255, 255, 255, 0.073);
}

.SideBar ul li i {
  margin-right: 12px;
}
